.mobile-menu{
    width: 100vw;
    height: 100vh;
    display: none;
    background-color: rgba(0,0,0,0.1);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 999 !important;
    opacity: 0;
    box-shadow: 0px 29px 80px rgba(0,0,0,0.1);
    transition: all 0.3s ease;
    transform: translateX(-100vw);
}




.mobile-menu.active{
    opacity: 1;
    transform: translateX(0);
}

.mobile-menu ul{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    list-style: none;
    margin-left: -2rem;
}

@media (max-width: 769px) {
    .mobile-menu{
        display: block;
    }
}